.form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  max-width: 665px;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  padding: 20px;
  border: 3px solid #c7a221;
  border-radius: 5px;
}

.form__title {
  text-align: center;
  text-shadow: 0 3px 11px #000;
  color: #fff;
  font-family: "Circle";
  font-size: 53px;
  font-weight: 700;
  line-height: 1;
  letter-spacing: -0.05em;
}

@media only screen and (max-width: 1023px) {
  .form__title {
    font-size: 46px;
  }
}

@media only screen and (max-width: 599px) {
  .form__title {
    font-size: 32px;
  }
}

.form__input-group {
  position: relative;
}

.form__input-group:not(:last-child) {
  margin-bottom: 18px;
}

.form__input-group .help-block.form-error {
  position: absolute;
  top: 100%;
  left: 2px;
  width: 100%;
  text-shadow: 0 3px 11px #000;
  color: #fff;
  font-family: "Circle";
  font-size: 18px;
  font-weight: 400;
  line-height: 1;
}

.form__checkbox .help-block.form-error {
  display: none;
}

.form__submit {
  text-align: center;
  margin-bottom: -40px;
}

.highlighted {
  color: #da7d0f;
  color: transparent;
  background-image: linear-gradient(to top, #c05302 0%, #da7d0f 100%);
  -webkit-background-clip: text;
}

.input {
  padding: 23px 17px 17px;
  width: 100%;
  color: #000;
  font-family: "Circle";
  font-size: 25px;
  font-weight: 300;
  line-height: 1.5677;
  border: 3px solid transparent;
  height: 64px;
  background-color: #fff;
  transition: border-color 0.3s;
}

@media only screen and (max-width: 1023px) {
  .input {
    height: 56px;
  }
}

.input:focus {
  border-color: #14b737;
}

.input.error {
  border-color: #ff0000 !important;
}

.input:placeholder {
  color: #000;
  font-family: "Circle";
  font-size: 22px;
  font-weight: 300;
  line-height: 1.5677;
}

.checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.checkbox__input {
  position: absolute;
  top: 0;
  height: 0;
  width: 0;
  opacity: 0;
  visibility: hidden;
}

.checkbox__input:checked ~ .checkbox__icon:before {
  transform: scale(1) rotate(45deg) translate(-1px, -1px);
}

.checkbox__text {
  display: inline-block;
  padding-left: 24px;
  text-shadow: 0 3px 11px #000000;
  color: #fff;
  font-size: 24px;
  font-weight: 400;
  line-height: 1.4;
  position: relative;
}

.checkbox__text a {
  color: inherit;
  text-decoration: none;
}

.checkbox__icon {
  display: inline-block;
  flex-shrink: 0;
  position: relative;
  min-width: 30px;
  width: 44px;
  height: 44px;
  border-radius: 3px;
  border: 3px solid #cfcfcf;
  background-color: #cfcfcf;
  margin-right: 18px;
}

.has-error .checkbox__icon {
  border-color: #ff0000;
}

.checkbox__icon:before {
  content: '';
  position: absolute;
  transition: transform 0.4s cubic-bezier(0.45, 1.8, 0.5, 0.75);
  transform: scale(0) rotate(45deg) translate(-1px, -1px);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 1;
  border-right: 2px solid #14b737;
  border-bottom: 2px solid #14b737;
  height: 15px;
  width: 12px;
}

[data-modal] {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  min-width: 320px;
  text-align: center;
  font-size: 0;
  transform: translate3d(0, 0, 0);
  overflow: auto;
  transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
  background-color: rgba(0, 0, 0, 0.5);
}

[data-modal]:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

[data-modal].is-open {
  opacity: 1;
  visibility: visible;
  z-index: 100;
}

[data-modal-container] {
  position: relative;
  z-index: 1;
  vertical-align: middle;
  display: inline-block;
  max-width: 1000px;
  margin: 0 auto;
}

.modal__container {
  width: 100%;
  background-color: #71225d;
  color: #fff;
}

.modal__container_error {
  max-width: 663px;
  padding: 79px 10px 87px;
}

.modal__container_success {
  max-width: 791px;
  padding: 50px 0 25px;
}

@media only screen and (max-width: 767px) {
  .modal__container {
    padding: 40px 15px;
  }
}

.modal__title {
  padding-bottom: 40px;
  font-family: "MyriadPro-Regular";
  font-size: 43px;
  font-weight: 400;
  line-height: 1.2;
  text-transform: uppercase;
}

.modal__logo {
  background-color: #1b1b1b;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 160px;
}

.modal__text {
  font-size: 24px;
  font-weight: 400;
  line-height: 3;
}

.modal__descr {
  margin-bottom: 36px;
  font-weight: 700;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.2;
}

.modal__descr-small {
  color: #ffffff;
  font-family: "MyriadPro-Regular";
  font-size: 18px;
  font-weight: 400;
  line-height: 1.2;
  padding: 0 104px 27px;
}

.modal__wish {
  margin-bottom: 34px;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.2;
  text-transform: uppercase;
}

.btn {
  display: inline-block;
  padding: 36px 75px 32px;
  border: 2px solid #924e9d;
  background-image: linear-gradient(45deg, #4f2169 0%, #c31e41 100%);
  text-decoration: none;
}

.btn__text {
  color: #fff;
  font-family: "Circle";
  font-size: 24px;
  font-weight: 700;
  line-height: 1.2;
  text-transform: uppercase;
}

.btn-cta {
  display: inline-block;
  position: relative;
  background-color: transparent;
  z-index: 5;
}

.no-touch .btn-cta:hover .btn-cta__shadow {
  opacity: 1;
  visibility: visible;
}

.btn-cta__shadow {
  position: absolute;
  top: 100%;
  left: 10%;
  right: 10%;
  box-shadow: 0 0 12px 10px rgba(0, 0, 0, 0.7);
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}

.btn-cta__inner {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 335px;
  height: 100px;
  background-image: url(../img/btn_active.png);
  background-size: cover;
  z-index: 5;
  text-shadow: 0 0 6px rgba(0, 0, 0, 0.3);
  color: #fff;
  font-family: "Acrom";
  font-size: 32px;
  font-weight: 900;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 0.02em;
}

@media only screen and (max-width: 1023px) {
  .btn-cta__inner {
    width: 307px;
    height: 91px;
  }
}

@media only screen and (max-width: 599px) {
  .btn-cta__inner {
    width: 154px;
    height: 46px;
    font-size: 24px;
  }
}
