.footer {
	padding: 43px 20px 23px;
	background-color: $black;
	display: flex;
	align-items: center;
	justify-content: center;
	min-height: 180px;
	@include md {
		min-height: 0;
		padding: 20px;
		align-items: flex-start; }
	@include sm {
		padding: 46px 20px; }
	&__logo {
		@include flex-size(26.4%);
		text-align: center;
		color: $white;
		font-family: $myriad;
		@include f(86px, 400, 1);
		@include r(1500);
		font-size: 50px;
		@include xl {
			@include flex-size(20%); }
		@include md {
			@include flex-size(40%); }
		&_right {
			@include md {
				display: none; } } }
	&__center {
		flex-grow: 1;
		text-align: center;
		@include sm {
			display: none; } }
	&__partners,
	&__payments {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		margin: 0 -8px 16px; }
	&__item {
		padding: 0 8px;
		& > * {
			display: inline-block; } }
	&__icon {
		text-align: center;
		.icon-min_age {
			display: inline-block; } } }

