//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$circle: 'Circle';
$myriad: 'MyriadPro-Regular';
$acrom: 'Acrom';

@font-face {
	@include fontdefault(normal, 300, $circle);
	@include fontpath('Circe-Light', 'Circe-Light', Circe-Light); }

@font-face {
	@include fontdefault(normal, 400, $circle);
	@include fontpath('Circe-Regular', 'Circe-Regular', Circe-Regular); }

@font-face {
	@include fontdefault(normal, 700, $circle);
	@include fontpath('Circe-Bold', 'Circe-Bold', Circe-Bold); }

@font-face {
	@include fontdefault(normal, 900, $acrom);
	@include fontpath('Acrom-ExtraBold', 'Acrom-ExtraBold', Acrom-ExtraBold); }

@font-face {
	@include fontdefault(normal, 400, $myriad);
	@include fontpath('MyriadPro-Regular', 'MyriadPro-Regular', MyriadPro-Regular); }
